body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

.header-inner {
  background: #121212;
  padding: 4rem;
  border: 4px solid #fff; }

@media (max-width: 568px) {
  .header-inner {
    padding: 0.5rem; } }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

h1 {
  margin: 1rem 0 0 0;
  line-height: 1.2; }

h2 {
  margin: 1rem 0 0 0;
  line-height: 1.2; }

h5 {
  margin: 1rem 0 0 0;
  line-height: 1.2;
  color: #61dafb; }

h5:nth-of-type(1) {
  color: #fff; }

.heading1 {
  margin: 8px 0;
  color: #121212; }

.logo-box {
  background: #000;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px 30px; }

.container {
  display: flex;
  padding: 1rem; }

.container .info,
.container .business {
  margin-top: 1rem;
  flex: 0 0 50%;
  display: table;
  border-collapse: collapse; }

.container .row {
  display: table-row;
  border-bottom: solid transparent 1rem; }

.container .row .td {
  display: table-cell;
  text-align: left;
  font-size: 16px; }

.container .row .td.title,
.title {
  text-align: left;
  font-size: 16px;
  color: orange;
  text-transform: uppercase;
  font-weight: 600; }

.container .row .td.second {
  padding-left: 0.5rem; }

a,
a:link,
a:active {
  text-decoration: none;
  color: #fff; }

@media (max-width: 768px) {
  .container {
    flex-direction: column; }
  .container .info .row .td {
    font-size: 14px; } }

@media (max-width: 500px) {
  .container {
    flex-direction: column;
    padding: 0.5rem; }
  .container .row .td {
    font-size: 16px; }
  .container .row .td.second {
    padding-left: 0.2rem; } }

.menu-container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  overflow: hidden; }
  .menu-container .navbar {
    width: 100%;
    max-width: 1440px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 8px 0;
    background: #ffb733; }
    .menu-container .navbar .nav-link {
      margin: 4px;
      padding: 8px;
      font-size: 16px;
      background: #222d32;
      color: #fff;
      border-radius: 4px; }
    .menu-container .navbar .active-link {
      background: #8b0000;
      color: #fff; }
  .menu-container .menu-img {
    width: 100%;
    height: auto; }

